

.splash-text-inner {
  word-wrap: break-word;
  overflow-wrap: break-word;
  cursor: pointer;
  color: #0ba7ec;
  opacity: 1;
  font-weight: 800;
}
.splash-img {
  opacity: 1;
}
.splash-container[class] {
    background: rgb(11,167,236);
    background: linear-gradient(180deg, rgba(11,167,236, 0.1) 0%, rgba(255,255,255, 0.1) 35%);
   width: 78% ;
}
.splash-container-for-buttton{
  
background-image: url('../assets/images/splash_img.jpg') ;
width: 100%;
background-repeat: no-repeat;
background-position:center ;
padding: 16%;
height:100vh;
}

.brcpxa {
  -webkit-transition: all 250ms ease;
  transition: all 250ms ease;
  border: none;
  margin: 5px;
  background-color: rgba(232,52,148, 1) !important;
  font-size: 1.3em;
  content: "";
  height: 10px;
  width: 10px;
  box-shadow: none!important;
  border-radius: 50%;
  outline: none;
}


